
  import { userInfo } from 'api/apis.js'
  import store from './store'
  
  export default {
    data() {
      return {
        timer: null
      }
    },
    onLaunch: () => {





    },
		onShow: function () {
      // 定时器更新用户数据
			this.timer = setInterval(() => {
        this.updateInfo()
      }, 30000)
		},
		onHide: function () {
			clearInterval(this.timer)
		},
    methods: {
      updateInfo() {
        uni.request({
          url: userInfo,
          method: 'GET',
          header: {
            Authorization: store.state.jwt
          },
          data: {},
          success: res => {
            if (res.data.success) {
              if (res.data.code === 401) {
                return
              }
              store.commit('savaUserInfo', res.data.data)
            }
          }
        });
      }
    }
	}
